<template>
  <div id="home">
    <nav-bar>
      <template v-slot:default>å¾ä¹¦åå¼</template>
    </nav-bar>

    <tab-control v-show="isTabFixed" @tabClick="tabClick" :titles="['çé','æ°ä¹¦','ç²¾é']"></tab-control>

    <div class="wrapper">
      <div class="content">
        <div ref="banref">
          <home-swiper :banners="banners"></home-swiper>

          <recommend-view :recommends="recommends"></recommend-view>
        </div>

        <tab-control @tabClick="tabClick" :titles="['çé','æ°ä¹¦','ç²¾é']"></tab-control>

        <goods-list :goods="showGoods"></goods-list>
      </div>
    </div>

    <back-top @bTop="bTop" v-show="isShowBackTop"></back-top>

  </div>
</template>

<script>
import HomeSwiper from "./ChildComps/HomeSwiper";
import NavBar from "components/common/navbar/NavBar";
import RecommendView from "./ChildComps/RecommendView";
import TabControl from "components/content/tabControl/TabControl"
import GoodsList from "components/content/goods/GoodsList";
import BackTop from "components/common/backtop/BackTop";
import {getHomeAllData,getHomeGoods} from "network/home";
import {ref, reactive, onMounted, computed, watchEffect, nextTick} from 'vue';
import BetterScroll from 'better-scroll';

export default {
  name: 'Home',

  setup(){

    let isTabFixed = ref(false);
    let isShowBackTop = ref(false);
    let banref = ref(null);
    const recommends = ref([]);

    //åååè¡¨æ°æ®æ¨¡å
    const goods = reactive({
      sales: {page:1, list:[]},
      recommend: {page:1, list:[]},
      new: {page:1, list:[]}
    })

    let currentType = ref('sales');

    const showGoods = computed(()=>{
      return goods[currentType.value].list
    })

    let bscroll = reactive({})

    let banners = ref([])
    onMounted(()=>{
      getHomeAllData().then(res=>{
        // console.log(res.goods);
        recommends.value = res.goods.data;
        banners.value = res.slides;
      })

      getHomeGoods('sales').then(res=>{
        // goods.sales.list = res.goods.data;
        goods['sales'].list = res.goods.data;
      })
      getHomeGoods('new').then(res=>{
        // goods.new.list = res.goods.data;
        goods['new'].list = res.goods.data;
      })
      getHomeGoods('recommend').then(res=>{
        // goods.recommend.list = res.goods.data;
        goods['recommend'].list = res.goods.data;
      })


      //åå»ºBetterScrollå¯¹è±¡
      bscroll = new BetterScroll(document.querySelector('.wrapper'),{
        probeType: 3, //1,2,3  3ï¼åªè¦å¨æ»å¨å°±è§¦åscrollæ¶é´
        click: true, //æ¯å¦åè®¸ç¹å»
        pullUpLoad: true //ä¸æå è½½æ´å¤ï¼é»è®¤æ¯false
      })

      //è§¦åæ»å¨äºä»¶
      bscroll.on('scroll', (position)=>{
        console.log(banref.value.offsetHeight);
        console.log(-position.y);

        isShowBackTop.value = isTabFixed.value = (-position.y) > banref.value.offsetHeight;
      })

      //ä¸æå è½½æ°æ®ï¼è§¦åpullingUp
      bscroll.on('pullingUp',()=>{
        console.log('ä¸æå è½½æ°æ®.....');

        const page = goods[currentType.value].page + 1;

        getHomeGoods(currentType.value,page).then(res=>{
          goods[currentType.value].list.push(...res.goods.data);
          goods[currentType.value].page += 1;
        })

        //å®æä¸æï¼ç­æ°æ®è¯·æ±å®æï¼è¦å°æ°æ®å±ç¤ºåºæ¥
        bscroll.finishPullUp();

        //éæ°è®¡ç®é«åº¦
        bscroll.refresh();

        console.log('contentheight:' + document.querySelector('.content').clientHeight);
        console.log('å½åç±»åï¼' + currentType.value + 'å½åé¡µï¼' + page);
      })

    })

    //çå¬ä»»ä½ä¸ä¸ªåéæåå
    watchEffect(()=>{
      nextTick(()=>{
        //éæ°è®¡ç®é«åº¦
        bscroll && bscroll.refresh();
      })
    })


    const tabClick = (index)=>{
      let types = ['sales','new','recommend'];

      currentType.value = types[index];

      nextTick(()=>{
        //éæ°è®¡ç®é«åº¦
        bscroll && bscroll.refresh();
      })
    }

    const bTop = ()=>{
      bscroll.scrollTo(0,0, 500)
    }
    return {
      recommends,
      tabClick,
      goods,
      showGoods,
      isTabFixed,
      isShowBackTop,
      banref,
      bTop,
      banners
    }
  },

  components: {
    HomeSwiper,
    NavBar,
    RecommendView,
    TabControl,
    GoodsList,
    BackTop
  }
}
</script>

<style scoped>
  #home {
    height: 100vh;
    position: relative;
  }

  .wrapper {
    position: absolute;
    top: 45px;
    bottom: 50px;
    left: 0;
    right: 0;
    overflow: hidden;
  }

  .content {

  }
</style>
