<template>
  <div>
    <div class="recommend">
      <div class="recommend-item" v-for="item in recommends.slice(0,4)" :key="item.id">
        <a href="" @click.prevent="goDetail(item.id)"><img v-lazy="item.cover_url" alt=""></a>
        <div>{{item.title}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import {useRouter} from "vue-router"

  export default {
    name: "RecommendView",
    props: {
      recommends: {
        type: Array,
        default(){
          return []
        }
      }
    },
    setup(){
      const router = useRouter();

      const goDetail = (id)=>{
        router.push({path:'/Detail',query:{id}})
      }

      return {
        goDetail
      }
    }
  }
</script>

<style scoped lang="scss">
  .recommend {
    display: flex;
    width: 100%;
    text-align: center;
    padding: 15px 0 30px;
    font-size: 12px;
    border-bottom: 8px solid #EEE;
  }
  .recommend-item {
    flex: 1;
    img {
      width: 70px;
      height: 70px;
      margin-bottom: 10px;
    }
  }
</style>